body.s,.s input,.s textarea,.s select,.s button,.s table{font-family:Helvetica}
body{position:relative;background-color:#f4f4f4;color:#000;-webkit-text-size-adjust:none}
img,fieldset{border:0}
ul,ol{list-style:none}
em,address{font-style:normal}
a{color:#000;text-decoration:none}
table{border-collapse:collapse}
.u_nc,hr{display:none !important}
.u_hc,.u_skip{visibility:hidden;overflow:hidden;position:absolute;left:-999em;width:0;height:0;font-size:0;line-height:0}
.u_vc{position:absolute;z-index:-1;font-size:1px;line-height:1px;color:transparent}
#ct{clear:both;width:100%;background-color:#fff}
#ct:after{display:block;clear:both;height:1px;margin-top:-1px;content:''}
select{height:29px;padding:0 20px 0 6px;border:1px solid #C9C9C9;border-radius:2px;background-color:#fff;background:url(#{$imgUrl-mobile}/im/sp_sel.png) no-repeat 100% 0;background-size:19px auto;-webkit-background-size:19px auto;font-size:13px !important;color:#000;vertical-align:middle;-webkit-appearance:none}
.a select{padding-top:1px}

