.lnb{overflow:hidden;position:relative;background:#f5f5f5;font-size:13px;color:#333;font-weight:bold;line-height:33px;}
.lnb:after{position:absolute;right:0;top:0;width:15px;height:33px;content: "";background: -webkit-gradient(linear, left top, right top, color-stop(0%,rgba(255,255,255,0)), color-stop(70%,rgba(255,255,255,0.99)), color-stop(100%,rgba(255,255,255,1))); /* Chrome,Safari4+ */}
.lnb ul{display: -webkit-flex;display: flex;height:33px;}
.lnb li{flex: 1 1 auto;-webkit-flex:1 1 auto;position:relative;text-align:center;padding:0 2px;letter-spacing:-1px}
.lnb li a{display:block;padding:0 13px;}
/*.lnb li:first-child a{padding-left:0;}
.lnb li:last-child a{padding-right:0;}*/
.lnb li.on a{color:#2f63c5;}
.lnb li:after{content:"";position:absolute;height:15px;width:1px;background:#d6d6d6;right:0;top:9px;}
.lnb li:before{content:"";position:absolute;height:15px;width:1px;background:#fff;left:0;top:9px;}
.lnb li:first-child:before{background:none}
.lnb li:last-child:before,.lnb li:last-child:after{background:none}
